html,body,div,main,nav,article,footer,p,ul,li,button,input,h1,h2,h3,h4,h5,textarea,label,span,a,form,img,section{margin:0;padding:0;border:0;outline:0;font:inherit;color:inherit;text-align:inherit;text-decoration:inherit;vertical-align:inherit;box-sizing:inherit;background:transparent}ul{list-style:none}img{display:block;width:100%;height:auto}input[type="password"],input[type="email"],input[type="text"],input[type="submit"],textarea,button{-webkit-appearance:none;-moz-appearance:none}button,input[type="submit"]{cursor:pointer}
